body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	height: 100vh;
	background-image: url(/static/media/piggy-big.300924f4.jpg);
	background-size: cover;
	background-position: center;
	color: black;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 18px;
}

.error {
	color: red;
	font-size: 10px;
}
.App {
	height: 400px;
	width: 300px;
	background-color: rgba(255, 255, 255, 0.4);
	border: 2px solid black;
	border-radius: 20px;
	box-shadow: 3px 1px 34px 0px rgba(243, 208, 59, 1);
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
#credit-value {
	text-align: center;
	padding: 10px;
	margin-top: 15px;
	border-radius: 20px;
	border-color: black;
	background-color: rgba(255, 255, 255, 0.4);
	font-size: 16px;
	width: 250px;
	max-width: 280px;
}
#credit-value:checked {
	border: none;

	/* background: none; */
}

form {
	margin: 20px 0;
}

div {
	text-align: center;
}

button {
	margin-top: 20px;
	border: 1px solid black;
	padding: 8px 40px;
	border-radius: 20px;
	cursor: pointer;
	background-color: rgba(255, 255, 255, 0.8);
	transition: 0.4s;
}

button:hover {
	box-shadow: 3px 1px 15px 0px rgba(243, 208, 59, 1);
	scale: 1.1;
}

h2 {
	text-align: center;
	color: white;
	margin-bottom: 5px;
	font-weight: normal;
}

input {
	margin-bottom: 10px;
}

span {
	color: gold;
}

